import Link from "next/link";
import {
  Activity,
  ArrowUpRight,
  RadioTower,
  Signal,
  Users,
  Zap,
} from "lucide-react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import type { Tower } from "@/lib/types";
import { Badge } from "@/components/ui/badge";
import StatCard from "@/components/stat-card";
import TowerCard from "@/components/tower-card";
import TowerStatusBadge from "@/components/tower-status-badge";
import { Button } from "@/components/ui/button";
import TowerMap from "@/components/tower-map";

async function getTowers(): Promise<Tower[]> {
  // In a real app, you'd fetch this from your API, and the API would get it from a database.
  // We created this API endpoint in a previous step.
  // For now, we'll keep using the mock data directly for simplicity on the frontend.
  // To use the API, you would do:
  // const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/towers`);
  // if (!res.ok) { throw new Error('Failed to fetch data'); }
  // return res.json();
  const { mockTowers } = await import("@/lib/mock-data");
  return mockTowers;
}


export default async function Dashboard() {
  const towers = await getTowers();

  const activeTowers = towers.filter((t) => t.status === "active").length;
  const avgSignal = Math.round(
    towers.reduce((acc, t) => acc + t.signalStrength, 0) /
      towers.length
  );
  const totalUsers = towers.reduce((acc, t) => acc + t.userLoad, 0);
  const totalEnergy = towers.reduce((acc, t) => acc + t.energyConsumption, 0).toFixed(1);

  return (
    <div className="flex-1 space-y-4">
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <StatCard
          title="Active Towers"
          value={activeTowers.toString()}
          icon={<RadioTower className="h-4 w-4 text-muted-foreground" />}
          description={`${Math.round((activeTowers / towers.length) * 100)}% of total`}
        />
        <StatCard
          title="Avg. Signal Strength"
          value={`${avgSignal} dBm`}
          icon={<Signal className="h-4 w-4 text-muted-foreground" />}
          description="Across all towers"
        />
        <StatCard
          title="Connected Users"
          value={totalUsers.toLocaleString()}
          icon={<Users className="h-4 w-4 text-muted-foreground" />}
          description="Real-time user load"
        />
        <StatCard
          title="Energy Consumption"
          value={`${totalEnergy} kWh`}
          icon={<Zap className="h-4 w-4 text-muted-foreground" />}
          description="Total this month"
        />
      </div>
      
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
        <div className="lg:col-span-4">
           <TowerMap towers={towers} />
        </div>
        <div className="lg:col-span-3">
          <Tabs defaultValue="list">
            <TabsList className="w-full">
              <TabsTrigger value="list" className="flex-1">Towers List</TabsTrigger>
              <TabsTrigger value="grid" className="flex-1">Grid View</TabsTrigger>
            </TabsList>
            <TabsContent value="grid">
                <div className="grid gap-4 md:grid-cols-1 lg:grid-cols-2 xl:grid-cols-2">
                    {towers.slice(0,4).map((tower) => (
                        <TowerCard key={tower.id} tower={tower} />
                    ))}
                </div>
            </TabsContent>
            <TabsContent value="list">
                 <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>Name</TableHead>
                        <TableHead>Status</TableHead>
                        <TableHead>
                          <span className="sr-only">Actions</span>
                        </TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {towers.map((tower) => (
                        <TableRow key={tower.id}>
                          <TableCell className="font-medium">{tower.name}</TableCell>
                          <TableCell>
                            <TowerStatusBadge status={tower.status} />
                          </TableCell>
                          <TableCell className="text-right">
                             <Button asChild variant="outline" size="sm">
                                <Link href={`/dashboard/towers/${tower.id}`}>
                                    Details <ArrowUpRight className="ml-2 h-4 w-4" />
                                </Link>
                             </Button>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
            </TabsContent>
          </Tabs>
        </div>
      </div>
    </div>
  );
}
